<template>
  <view class="container">
    <view v-if="showSwiper" class="detail_bg">
      <swiper class="detail_bg" :autoplay="true" indicator-color="white">
        <swiper-item v-for="(image, index) in info.images_url" :key="index">
          <image :src="image.http_img" class="image_item"></image>
        </swiper-item>
      </swiper>
    </view>
    <view class="content">
      <view class="content_top" style="position: relative">
        <view class="title">{{ info.name }}</view>
      </view>
      <view class="des-content" style="padding: 0 15px">
        {{ info.released_time ? info.released_time : info.c_time }}
      </view>
      <view class="des-content">
        <rich-text :nodes="info.des"></rich-text>
      </view>
    </view>
  </view>
</template>

<script>
import shareMixins from '@/mixins/shareMixins'
import { getWxNoticeDetail } from '@/api/home/index'
import { formatRichText } from '@/utils/index'
export default {
  mixins: [shareMixins],
  data() {
    return {
      info: {
        images_url: [],
      },
      notice_id: '',
      showSwiper: false,
    }
  },
  onLoad(options) {
    this.notice_id = options.notice_id
    this.getWxNoticeDetail()
  },
  methods: {
    async getWxNoticeDetail() {
      let res = await getWxNoticeDetail({
        notice_id: this.notice_id,
      })
      if (res.code == 200) {
        let data = res.data
        data.des = formatRichText(data.des)
        if (
          (data.images_url && data.images_url.length > 0) ||
          (data.video_url && data.video_url.length > 0)
        ) {
          this.showSwiper = true
        }
        this.info = data
      } else {
        console.error(res)
      }
    },
  },
}
</script>

